const deleteElementBySelector = (selector) => {
  if (!selector) return;
  const divToRemove = document.querySelector(selector);
  if (!divToRemove) return;
  divToRemove.parentNode.removeChild(divToRemove);
};


const addClickEventListeners = () => {
  debugger;
  function showWeatherDetails() {
    const hourDetails = this.querySelector('.hour-details');

    if(hourDetails.style.maxHeight) {
      hourDetails.style.maxHeight = "0";
      hourDetails.style.padding = "0";
      this.style.boxShadow = "none";
    }
    else {
      hourDetails.style.maxHeight = hourDetails.scrollHeight + "20px";
      hourDetails.style.padding = "21px 16px";
      this.style.boxShadow = "inset 0px 0px 2px 2px rgba(0,0,0,0.25)";
    }
  }

  const hourContainers = document.querySelectorAll('.hour-container');

  for (let container of hourContainers) {
    container.addEventListener('click', showWeatherDetails);
  }
};



